<template>
  <div class="body-bg">
    <headerBar :header-data="headerData" />
    <div class="container-ca">
      <div class="login-cainfo">
        <div class="ca-icon"><van-image fit="cover" width="100%" height="100%" :src="imageList.img1" /></div>
        <div class="ca-text">CA移动证书</div>
      </div>
      <div class="login-form">
        <van-cell-group>

          <div class="input-item">
            <van-field
              ref="name"
              v-model="user.userAccount"
              v-validate="'required|mmLength'"
              clearable
              is-highlight
              is-title-latent
              placeholder="请输入移动证书Pin码"
              name="PIN"
              :error-message="errors.first('PIN')"
            />
          </div>

        </van-cell-group>
        <van-notice-bar
          style="margin-top:15px;"
          left-icon="info-o"
          text="此密码将作为您以后的移动证书登录密码,请妥善保管！"
        />
        <div class="login-button">
          <van-button round type="info" @click="goScan()">扫码解密</van-button>
        </div>
      </div>
    </div>

    <!-- 证书安装成功弹窗 begin -->
    <van-popup v-model="show" closeable round style="width:90%;height:320px;">
      <div class="information-tips">
        <div class="tips-icon success" />

        <!-- 警告信息 -->
        <!-- <div class="tips-icon info" /> -->
        <!-- 失败 -->
        <!-- <div class="tips-icon error" /> -->
        <div class="information-tips-tit">CA移动证书下载成功</div>
        <div class="information-tips-txt">CA移动证书下载成功</div>

        <div class="information-tips-btn">
          <ul>
            <li><van-button plain type="info" size="large" round>返回首页</van-button></li>
            <li><van-button type="info" size="large" round>立即登录</van-button></li>
          </ul>
        </div>
      </div>
    </van-popup>

    <!-- 证书安装成功弹窗 end -->
  </div>

</template>

<script>
import headerBar from '@/components/header/headerBar.vue'
import { pageRouter, NativeCaJS, NativeJS } from '@/common/js/mixin.js'
import { loadSearch } from '@/common/js/catch'
import { caSmJJm, getSfxx } from '@/store/module.js'
export default {
  name: 'CaCz',
  components: {
    headerBar
  },

  mixins: [pageRouter, NativeCaJS, NativeJS],
  data() {
    return {
      Native_JmMessage: '',
      headerData: {
        title: '扫码解密',
        leftUrl: 'back',
        leftText: '首页'
      },
      user: {
        userAccount: ''
      },
      PIN: '', // 密码
      token: '',
      imageList: {
        img1: require('@/assets/images/login/icon-jiemi.png'),
        tipsimg1: require('@/assets/images/icon-success.png'), /* 成功 */
        tipsimg2: require('@/assets/images/icon-info.png'), /* 警告 */
        tipsimg3: require('@/assets/images/icon-error.png') /* 失败 */
      },
      titleImage: true, // 标题图片
      eyesChange: true, // 是否展示密码
      show: false
    }
  },
  watch: {
  // 二维码数据
    Native_ScanData(val) {
      if (val) {
        console.info('val===1==', val.name)
        this.getScanData(val.name)
      }
    },
    // ca解密结果
    Native_JmMessage(val) {
      console.info('Native_JmMessage===0==', val)
      if (val) {
        console.info('Native_JmMessage===1==', val)
        this.postJmXxData(val)
      }
    }
  },

  methods: {
    // 控制是否展示密码
    showEye() {
      this.eyesChange = !this.eyesChange
    },
    // 下载
    goScan() {
      this.$validator.validate().then(result => {
        if (result) {
          this.NativeScan()
        }
      })
    },
    getScanData(token) {
      this.token = token
      getSfxx.token = token
      this.asyncApiModule(getSfxx).then(res => {
        res = JSON.parse(res)
        this.zjmessage = res
        if (res !== null) {
          console.log('res=====', res)
          console.log('res.kzzd=====', res.kzzd)
          const ca = loadSearch('caListItem')[0]
          const ca1 = JSON.parse(ca)
          const pins = loadSearch('caListPin')[0]
          const pin = {
            'CAXLH': ca1.serialNo,
            'newPin': pins,
            'sjjmid': res.sjjmid,
            'jmxx': res.jmxx
          }
          if (process.env.NODE_ENV !== 'development') {
            try {
              this.Native_JmMessage = window.myapp.envelopeDecrypt(JSON.stringify(pin))
            } catch (err) {
              console.log(err)
            }
            this.$bridge.callhandler('envelopeDecrypt', res, (data) => {
              const datas = JSON.parse(data)
              console.log('goScanView------', data)
              this.Native_JmMessage = datas
            })
          } else {
            this.$toast('返回原生页面')
          }
        }
        // this.needIdCard = true
        // this.$toast.success('扫描成功')
      }).catch(error => {
        this.$toast(error)
        console.log('error=====', error)
      })
    },
    postJmXxData(value) {
      console.log('解密上传数据=====', value)
      const data = { 'value': value, 'token': this.token }
      caSmJJm.data = data
      this.asyncApiModule(caSmJJm).then(res => {
        console.log('解密返回数据===2==', res)
      }).catch(error => {
        this.$toast(error)
        console.log('解密失败==2==', error)
      })
    },
    showPopup() {
      this.show = true
    }

  }

}

</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
